import React, { useContext } from 'react';
import { Context } from '../context';
import Track from './track'

const Tracks = () => {
    const [state] = useContext(Context);
    const { track_list, heading } = state;
    if(track_list!==undefined){
        return (
            <>
                <div style={{fontSize:"30px"}}><strong>{heading}</strong></div>
                <div className="row">
                    
                    {track_list.map( (song, i) =>(
                        <div className="col-lg-3 col-sm-6 col-xs-12" key={i} style={{paddingLeft:"45px"}}><Track id={song.track.track_id} track={song.track}/></div>
                    ))}
                    
                </div>
            </>
        )
    }
};

export default Tracks;