import axios from 'axios';
import React from 'react';
import { useState, useEffect} from 'react'
import { Descriptions } from 'antd';

const Lyrics = props => {
    const [track, settrack] = useState({});
    const [lyrics, setlyrics] = useState({})
    useEffect(() => {
        axios
        .get(
            `https://cors-anywhere.herokuapp.com/http://api.musixmatch.com/ws/1.1/track.lyrics.get?track_id=${props.match.params.code}&apikey=${process.env.REACT_APP_A}`
            )
        .then(res => {
            console.log(res);
            let lyrics = res.data.message.body.lyrics;
            setlyrics({ lyrics });
            return axios.get(
              `https://cors-anywhere.herokuapp.com/http://api.musixmatch.com/ws/1.1/track.get?track_id=${props.match.params.code}&apikey=${process.env.REACT_APP_A}`
            );
          })
          .then(res => {
            console.log(res);
            let track = res.data.message.body.track;
            settrack({ track });
          })
          .catch(err => console.log(err));
    }, [props.match.params.code])
    // let track1 = track.track.track_name;
    // let album1 =  track.track.album_name;
    // let artist1 = track.track.artist_name;
    // let lyrics1 = lyrics.lyrics.lyrics_body;
    if(track === undefined || lyrics === undefined || Object.keys(track).length === 0 || Object.keys(lyrics).length === 0){
      return (<div><h1>Loading...</h1></div>)}
    else{
      return (
          <div>
            <Descriptions
              title="LYRICS"
              bordered
              column={{ xxl: 1, xl: 1, lg: 1, md: 1, sm: 1, xs: 1}}
            >
              <Descriptions.Item label="Track" style={{fontSize:"25px"}}>{track.track.track_name}</Descriptions.Item>
              <Descriptions.Item label="Album" style={{fontSize:"25px"}}>{track.track.album_name}</Descriptions.Item>
              <Descriptions.Item label="Artists" style={{fontSize:"25px"}}>{track.track.artist_name}</Descriptions.Item>
              <Descriptions.Item label="Lyrics" style={{fontSize:"25px"}}>{lyrics.lyrics.lyrics_body}</Descriptions.Item>
            </Descriptions>
          </div>
      )
    }
}

export default Lyrics
