import './App.css';
import Head from './components/header';
import SearchLyrics from './components/search';
import Tracks from './components/tracks';
import { ContextController } from "./context";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import Lyrics from './components/lyrics';

function App() {
  return (
    <ContextController>
      <Router>
        <div className="App">
            <Head/> 
            <SearchLyrics/> 
            <div style={{fontSize:"30px", width:"70%", margin:"20px auto"}}>Welcome to the universe of lyrics where stars of all genres and ages shine.</div>
            <Switch>
              <Route exact path="/" component={Tracks} />
              <Route exact path="/track/:code" component={Lyrics} />
            </Switch>
        </div>
      </Router>
    </ContextController>
  );
};

export default App;
