import React, { useContext, useState, useEffect } from 'react'
import { Input } from 'antd';
import axios from 'axios';
import { Context } from '../context'

const { Search } = Input;

const SearchLyrics = () => {
    const [state, setState] = useContext(Context);
    const [input, setinput] = useState("");
    
    useEffect(() => {
        axios
            .get(
                `https://cors-anywhere.herokuapp.com/http://api.musixmatch.com/ws/1.1/track.search?q_track=${input}&page_size=12&page=1&s_track_rating=desc&apikey=${process.env.REACT_APP_A}`
            )
            .then(res => {
                console.log(res);
                setState({track_list:res.data.message.body.track_list, heading:"Track Result"});
            })    
            .catch(error => {
                console.log(error);
            })
    },[input]);

    const load = (value, event) => {
        event.preventDefault();
        setinput(value);
    }
    return (
        <>
            <Search placeholder="Name of Song" enterButton  style={{width:"80%", marginTop:"20px"}} onSearch={load}/>
        </>
    )
}

export default SearchLyrics