import React from 'react'
import { Layout } from 'antd';
import { AudioOutlined } from '@ant-design/icons';
import { Link } from 'react-router-dom';

const { Header} = Layout;

const Head = () => {
    const refresh = () =>{
        window.location.reload();
    }
    return (
        <Layout className="layout">
            <Header>
                <Link to={`/`}><h1 className="head" style={{marginTop:"15px"}} onClick={refresh}>Get Your Lyrics <AudioOutlined /></h1></Link>
            </Header>
        </Layout>
    )
}

export default Head
