import React from 'react'
import { Card } from 'antd';
import { Button } from 'antd';
import { Link } from 'react-router-dom'

const Track = props => {
    const { id, track } = props;
        return (
            <div className="site-card-border-less-wrapper">
                <Card title={track.track_name} bordered={false} style={{ width: 300 }}>
                    <p style={{height:"25px", overflow:"hidden", fontSize:"15px"}}><strong>ALBUM : </strong>{track.album_name}</p>
                    <p style={{height:"25px", overflow:"hidden", fontSize:"15px"}}><strong>ARTIST : </strong>{track.artist_name}</p>
                    <Link to={`/track/${id}`}><Button type="primary" block>Lyrics</Button></Link>
                </Card>
            </div>
        )
}
export default Track;