import React, { useState, useEffect, createContext } from "react";
import axios from "axios";

export const Context = createContext();

export function ContextController({children}) {
  let intialState = {
    track_list: [],
    heading: ""
  };

  const [track, settrack] = useState(intialState);

  useEffect(() => {
    axios
      .get(
        `https://cors-anywhere.herokuapp.com/http://api.musixmatch.com/ws/1.1/chart.tracks.get?page=1&page_size=20&country=us&f_has_lyrics=1&apikey=${process.env.REACT_APP_A}`
      )
      .then(res => {
         console.log(res.data);
        settrack({
          track_list: res.data.message.body.track_list,
          heading: "Top Tracks"
        });
      })
      .catch(err => console.log(err));
  }, []);

  return (
    <Context.Provider value={[track, settrack]}>{children}</Context.Provider>
  );
}
